  .scroll-to-top {
    position: fixed; /* Stays in the same place even when the page is scrolled */
    bottom: 20px; /* Position from the bottom of the page */
    right: 20px; /* Position from the right of the page */
    width: 40px; /* Width of the button */
    height: 40px; /* Height of the button */
    background-color: #00008b; /* Background color of the button */
    color: #fff; /* Icon color */
    display: flex; /* Use flexbox to center the icon */
    justify-content: center; /* Center the icon horizontally */
    align-items: center; /* Center the icon vertically */
    cursor: pointer; /* Change cursor to pointer when hovering over the button */
    opacity: 0.7; /* Slightly transparent */
    transition: opacity 0.3s; /* Smooth transition for opacity */
  }
  
  .scroll-to-top:hover {
    opacity: 1; /* Fully opaque on hover */
    background-color:#4aa3df ;
  }
  
  .scroll-to-top i {
